export default {
	data() {
		return {
			subjectList:[],
			value:'',
			userInfo:'',
		}
	},
	created(){

	},
	onLoad() {
		uni.getStorage({
		    key: 'user_token',
		    success: (res)=> {
					console.log(res)
		      this.user_token = res.data;
		    }
		});
		uni.getStorage({
		    key: 'userInfo',
		    success: (res)=> {
					console.log(res)
		      this.userInfo = res.data;
		    }
		});
		// this.getBanner();
		//this.getClass();
	},
	methods:{
		Todetail(e){// 跳转到详细视频
			uni.navigateTo({
			    url: 'class?class_id='+e
			});
		},
		inputchange(e){
			console.log(e);
			this.value = e.detail.value;
		},
		getClass(){// 获取视频列表
		  if(this.value == ''){
				this.$msg('请输入内容搜索')
			};
			uni.request({
				url: this.$url+'api/classes/index'+this.user_token,
				method: 'post',
				data: {},
				success: res => {
					console.log(res,'课程');
					if(res.data.code == 10000){
						this.subjectList = res.data.data.list;
						this.total = res.data.data.total_num;
					}else{
						this.$msg(res.data.msg)
					}
				},
			})
		},
	},
}
